.header {
  background-image: $primary-gradient;
}

.navigation{
  position: fixed;
  top: 0;
  width: 100%;
  transition: .3s ease;
  padding-top: 30px;
  z-index: 1;
  @include desktop {
    padding-top: 5px;
    background: rgba($color: $primary-color, $alpha: .7);
  }
  &.nav-bg{
    padding-top: 5px;
    background: $primary-color;
    .navbar-brand{
      img{
        max-height: 50px;
      }
    }
  }
}


.headroom {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transition: all .5s ease-in-out;
  z-index: 3;
}

.headroom--unpinned {
  top: -150px;
}

@include desktop {
  .headroom--unpinned {
    top: 0;
  }
}

.headroom--pinned {
  top: 0;
}

.banner {
  padding-top: 270px;
  padding-bottom: 200px; 
  position: relative;
  z-index: 2;
  @include desktop {
    padding-top: 200px;
    padding-bottom: 140px; 
  }
  @include tablet {
    padding-top: 150px;
    padding-bottom: 110px; 
  }
  p {
    font-size: 24px;
  }
}

.navbar {
  &-brand{
    img{
      @include desktop {
        max-height: 50px;
      }
    }
  }
  .nav-item {
    position: relative;
    margin: 0 20px;

    .nav-link {
      font-size: 16px;
      color: $white !important;
      padding: 30px 3px;

      &::before {
        position: absolute;
        content: "";
        height: 1px;
        width: 100%;
        background: $white;
        top: 100%;
        left: 0;
        transform: translateY(-50%);
        opacity: 0;
        transition: .3s ease;
        z-index: 1;
      }
  
      &.active {
        &::before {
          top: 50%;
          opacity: 1;
        }
      }
    }
  }
}

/* gallary */
.filter-controls {
  margin-bottom: 50px;

  li {
    position: relative;
    padding: 5px;
    margin: 0 12px;
    cursor: pointer;

    &::before {
      position: absolute;
      content: "";
      height: 1px;
      width: 100%;
      background: $text-color-dark;
      top: 100%;
      left: 0;
      transform: translateY(-50%);
      opacity: 0;
      transition: .3s ease;
      z-index: 1;
    }

    &.active {
      color: $text-color-dark;

      &::before {
        top: 50%;
        opacity: 1;
      }
    }
  }
}

.filter-items{
  #loadMore{
    text-decoration: underline;
    font-weight: 600;
  }
  .it-hide{
    display: none;
  }
  .it-show{
    display: flex;
  }
}

/* google map */
#map_canvas {
  height: 670px;
}

.form-control {
  height: 45px;
  border-color: $border-color;

  &::placeholder {
    color: #9fa3a7;
  }
}

textarea.form-control {
  height: 180px !important;
}

.contact {
  h6 {
    color: #c2c2c2;
    margin-bottom: 0;
  }
}

/* footer */
.footer {
  background-image: linear-gradient(360deg, rgb(59, 71, 158) 0%, rgb(255, 255, 255) 100%);

  &-menu {
    border-bottom: 1px solid #999fc5;
  }

  &-content {
    opacity: .5;
    padding: 40px 0;

    p,
    a {
      color: $white;
      font-size: 12px;
      margin-bottom: 0;
    }

    a {
      text-transform: uppercase;
    }
  }
}