/*  typography */
@import url('https://fonts.googleapis.com/css?family=Muli:300,400,600,700,800,900|Poppins:300,400,500,600,700,900');

body {
  line-height: 1.5;
  font-family: $primary-font;
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
  color: $text-color;
}

p, .paragraph {
  font-weight: 400;
  color: $text-color;
  font-size: 16px;
  line-height: 1.5;
  font-family: $primary-font;
}

.paragraph-lg{
  font-size: 18px;
}

h1,h2,h3,h4,h5,h6 {
  color: $text-color-dark;
  font-family: $secondary-font;
  font-weight: 400;
  line-height: 1;
}

h1, .h1{
  font-size: 60px;
}

h2, .h2{
  font-size: 50px;
}

h3, .h3{
  font-size: 40px;
}

h4, .h4{
  font-size: 30px;
}

h5, .h5{
  font-size: 20px;
}

h6, .h6{
  font-size: 14px;
}